@import url(https://fonts.googleapis.com/css?family=Work+Sans:400,500,700|Nunito:400,700|Source+Sans+Pro&display=swap);
body {
  --color-white: #FFFFFF;
  --color-primary: #0D5283;
  --color-bg: #fbc01a;
  --color-bg-tint: #efa318;
  --padding-sides: 30px; /* Horizontal padding between sides of screen and content */
  font-family: 'Work Sans', Arial, sans-serif;
  width: 100vw;
  height: 100vh;
  font-size: 16px;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
