.BackgroundContainer {
  width: 100vw;
  height: 100vh;
  background: var(--color-bg);
  display: flex;
  flex-flow: column;
}
.CheckedInStatus {
  color: var(--color-white);
  padding: 0 var(--padding-sides);
}
.CheckedInStatus .ActionRow {
  margin: 20px 0;
}
.CheckedInStatus .btn {
  display: inline-block;
  margin-right: 8px;
  padding: 10px 12px;
  border-radius: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 13px;
  color: var(--color-white) !important;
}
.CheckedInStatus .btn-primary {
  background: var(--color-bg-tint);
}
.Header {
  color: var(--color-white);
  padding: 20px var(--padding-sides);
  display: flex;
  background: var(--color-bg);
}
.Header .left {
  flex: 1 1;
}
.Header .right {
  flex: 1 1;
  text-align: right;
}
.Header .fas {
  margin-left: 16px;
  font-size: 18px;
}
.Header h1 {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  margin-top: 1px;
}
.Header a {
  color: var(--color-white);
  text-decoration: none !important;
}
.ContainerPanel {
  flex: 1 1;
  background: var(--color-white);
  border-radius: 30px 30px 0 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.TabBar {
  border-bottom: 2px solid #eee;
  padding: 0 var(--padding-sides);
  padding-top: 18px;
}

.TabBar .TabItem {
  display: inline-block;
  margin-right: 20px;
  padding-bottom: 8px;
  color: #777;
  font-weight: 500;
}

.TabBar .TabItem.active {
  color: var(--color-primary) !important;
}
.StudyBox {
  --padding-inside: 12px;
  margin: 0 calc(var(--padding-sides) - var(--padding-inside));
  margin-top: 20px;
  border-radius: 12px;
  padding: 14px var(--padding-inside);
}

.StudyBox .intro-label {
  margin-bottom: 6px;
}

.StudyBox .ContactList {
  border-top: 2px solid #eee;
  border-bottom: 2px solid #eee;
  padding: 10px 0;
}

.StudyBox .pic {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  vertical-align: middle;
  margin-right: 10px;
}

.StudyBox .Contact:not(:last-child) {
  margin-bottom: 10px;
}
.Login .aligner {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.Login .aligner .logoPlaceholder {
  flex: 1 1;
}
.Login .btn-login {
  background: #fff;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
  margin: 16px var(--padding-sides);
  margin-top: 0;
  padding: 14px 18px;
  border-radius: 8px;
}
.Login .aligner {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.Login .aligner .logoPlaceholder {
  flex: 1 1;
}
.Login .btn-login {
  background: #fff;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
  margin: 16px var(--padding-sides);
  margin-top: 0;
  padding: 14px 18px;
  border-radius: 8px;
}
.Account .pic {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  margin: 0 auto;
  margin-top: 30px;
}
.Account .accountName {
  text-align: center;
  color: #fff;
  font-size: 18px;
  margin-top: 8px;
  margin-bottom: 50px;
  font-weight: 500;
}
.Account .options a {
  display: block;
  color: #fff;
  text-decoration: none;
  text-align: center;
  margin-bottom: 30px;
  font-size: 18px;
  background: var(--color-bg-tint);
  padding: 14px 0;
}
.Page.AddFriends {
  min-height: 100vh;
  background: #f5f5f5;
}
.Page.AddFriends .add-form {
  padding: 20px 30px;
}
.Page.AddFriends .add-form input {
  margin: 0;
  border: none;
  border-radius: 0;
  outline: none !important;
  padding: 12px 16px;
  border-radius: 8px;
  box-sizing: border-box !important;
  display: block;
  width: 100%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
  margin-top: 12px;
}
.Page.AddFriends .add-form button {
  border: none;
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
  border-radius: 8px;
  padding: 12px;
  outline: none !important;
  box-sizing: border-box !important;
  display: block;
  float: right;
  margin-top: 12px;
}
.Page.AddFriends .FriendList {
  padding: 20px 30px;
  clear: both;
}
.Page.SetupAccount .BackgroundContainer {
  display: flex;
  flex-direction: column;
}
.Page.SetupAccount .welcome-message {
  text-align: center;
  margin-top: 70px;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
}
.Page.SetupAccount .input-form {
  color: #fff;
  flex: 1 1;
  text-align: center;
  margin-top: 40px;
}
.Page.SetupAccount .input-form input {
  display: block;
  outline: none !important;
  border: none;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  margin: auto;
  padding: 12px 16px;
  margin-top: 4px;
  border-radius: 8px;
}
.Page.SetupAccount footer {
  display: block;
  width: 100%;
  text-align: right;
  padding: 0 30px;
  box-sizing: border-box;
  background: var(--color-bg-tint);
}
.Page.SetupAccount footer .confirm-btn {
  padding: 12px 16px;
  background: rgba(0, 0, 0, 0.2);
  border: none;
  color: var(--color-white);
  border-radius: 0;
  outline: 0 !important;
}
.Page.CheckIn {
  background: #f5f5f5;
  height: 100vh;
}
.Page.CheckIn .field {
  margin-top: 30px;
  padding: 0 30px;
}
.Page.CheckIn .field input, .Page.CheckIn .field textarea, .Page.CheckIn .field select {
  display: block;
  outline: none !important;
  border: 0;
  padding: 12px 16px;
  border-radius: 8px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.01);
  margin-top: 4px;
  background: #fff;
}
.Page.CheckIn .btn {
  display: inline-block;
  margin-right: 8px;
  padding: 10px 12px;
  border-radius: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 13px;
  color: var(--color-white) !important;
  margin-top: 30px;
  margin-left: 30px;
}
.Page.CheckIn .btn-primary {
  background: var(--color-bg);
}
.Profile .accountName {
  text-align: center;
  color: #000000;
  font-size: 35px;
  margin-top: 8px;
  margin-bottom: 10px;
  font-weight: 500;
}
.Profile .accountUsername {
  text-align: center;
  color: #c4c4c4;
  margin-top: 8px;
  margin-bottom: 10px;
  font-weight: 500;
}
.Profile .pic {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  margin: 0 auto;
  margin-top: 20px;
}
.Profile .courseList {
  color: #0D5283;
  list-style-type: none;
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
  margin: 20px;
}
.Profile .infoTitle {
  padding-top: 15px;
  padding-left: 30px;
}
.Profile .center {
  text-align: center;
  margin: 0 auto;
}
.Profile .defaultFontSize {
  font-size: 16px;
  font-weight: 500;
}
.Profile hr {
  width: 90%;
}
.Profile .icon {
  color: #0D5283;
  font-size: 25px;
  margin-right: 5px;
}
.Profile .description {
  color: #0D5283;
  padding-left: 30px;
  padding-right: 30px;
}
.Profile .edit {
  float: right;
  padding-right: 15px;
  text-decoration: underline;
  color: #c4c4c4;
}
.Friends .friendsList {
  list-style-type: none;
}
.Friends .box {
  padding: 20px;
  box-shadow: 4px 4px 4px 2px #c4c4c4;
  margin-top: 15px;
  margin-bottom: 15px;
  margin-left: 30px;
  margin-right: 30px;
}
.Friends .pic {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  float: left;
  margin-right: 10px;
}
.Friends .name {
  font-weight: 500;
  font-size: 18px;
}
.Friends ul {
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}
.Friends .searchbar {
  font-size: 20px;
  padding: 10px 15px 10px 15px;
  background-color: #efefef;
  margin: 15px 15px 0px 15px;
  border-radius: 30px;
  color: #c4c4c4;
}
.Friends .pageTitle {
  font-size: 25px;
  margin: 15px;
}
.Friends .friendIcon {
  float: right;
}
.Friends #searchIcon {
  float: right;
}
